<template>
  <div class="sku">
    <div class="sku-top">
      <img width="30" :src="basicInfo.pic" alt />
      <div class="sku-top-rigth">
        <p v-html="basicInfo.name"></p>
        <p>￥{{basicInfo.minPrice}}</p>
        {{this.title1}}
      </div>
    </div>
    <div class="sku-gg">
      <div class="sku-box">
        <!-- if判断 -->
        <div v-if="skugg1!=null">
          <p class="sku-p">{{skugg.name}}</p>
          <div class="sku-list">
            <div :class="{'action':k==num}" v-for="(v,k) in skugg.childsCurGoods" :key="v.id">
              <div @click="djcolor(v.name,k)" class="sku-itme">{{v.name}}</div>
            </div>
          </div>
          <p class="sku-p">{{skugg1.name}}</p>
          <div class="sku-list">
            <div
              :class="{'action':key==number}"
              v-for="(itme,key) in skugg1.childsCurGoods"
              :key="itme.id"
            >
              <div @click="djcolor1(itme,key)" class="sku-itme">{{itme.name}}</div>
            </div>
          </div>
        </div>
        <!-- if判断 -->
        <div v-else>
          <p class="sku-p">{{skugg.name}}</p>
          <div class="sku-list">
            <div :class="{'action':k==num}" v-for="(v,k) in skugg.childsCurGoods" :key="v.id">
              <div @click="djcolor(v.name,k)" class="sku-itme">{{v.name}}</div>
            </div>
          </div>
        </div>

      </div>
    </div>
    <div class="goos">
      <p>购买数量</p>
      <div>
        <van-stepper v-model="value" @plus="aa" />
      </div>
    </div>
    <van-button @click="add(basicInfo)" type="danger" size="large">加入购物车</van-button>
  </div>
</template>

<script>
export default {
  name: "Skupoup",
  props: ["basicInfo", "skugg", "skugg1"],
  data() {
    return {
      num: -1, //动态class样式1
      number: -1, //动态class样式1
      title1: "", //定义获取尺寸的
      value: 1, //计数器
      check: false, //控制是否是选中的
      name: "" //定义获取颜色的
    };
  },
  methods: {
    //点击获取尺寸的name值
    djcolor(v, k) {
      this.num = k;
      this.title1 = v; //获取尺寸name值
    },
    //点击获取颜色的name值
    djcolor1(itme, key) {
      this.number = key;
      this.name = itme.name; //  获取颜色的name值
      // console.log(this.name);
    },
    aa() {},
    add(itme) {
      this.$nextTick(() => {
        setTimeout(() => {
          let token = localStorage.getItem("yx-token");
          if (!token) {
            this.$toast.fail("请先登陆账号");
            this.$router.push("/login");
            return false;
          } else {
            this.$toast.success("加入购物成功");
            console.log("已登录账号");
            if (this.title1 == "" ) {
              this.$toast("请选择规格");
              return false;
            }
            let obj = new Object();
            obj.id = itme.id; //添加商品id
            obj.img = itme.pic; //添加商品图片
            obj.name = itme.name; //添加商品标题
            obj.price = itme.minPrice; //添加商品价钱
            obj.tit = this.skugg.name; //添加商品尺寸的name名称
            obj.tit1 = this.title1; //添加商品的尺寸
            obj.num = this.value; //添加商品的数量
            obj.check = this.check; //添加商品   是否是选中
            obj.name = this.name; //添加商品颜色的name名称
            this.$store.commit("add", obj); //往vuex添加的方法
            this.$router.push("/home/goods"); //添加完成跳转到购物车页面
            console.log(obj);
          }
        }, 1000);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.sku {
  .sku-top {
    width: 100%;
    border-bottom: 1px solid gainsboro;
    padding: 0.2rem;
    display: flex;
    img {
      width: 1.4rem;
      height: 1.4rem;
    }
    .sku-top-rigth {
      width: 4rem;
      margin-left: 0.3rem;
      p:nth-of-type(1) {
        width: 100%;
        margin-top: 0.3rem;
      }
      p:nth-of-type(2) {
        width: 100%;
        margin-top: 0.3rem;
        color: red;
      }
    }
  }
  .sku-gg {
    width: 100%;
    padding: 0.4rem 0;
    border-bottom: 1px solid gainsboro;
    .sku-box {
      width: 100%;
      height: 100%;

      .sku-p {
        font-size: 0.2rem;
        margin-left: 0.24rem;
        // margin-top: .2rem;
      }
      .sku-list {
        width: 100%;
        display: flex !important;
        // justify-content: space-between !important;
        // align-items: center !important;
        .sku-itme {
          //   width: 30%;
          padding: 0.1rem 0.1rem;
          border-radius: 0.06rem;
          margin-top: 0.1rem;
          margin-left: 0.1rem;
          border: 0.01rem solid gainsboro;
        }
        .action {
          .sku-itme {
            border: 0.01rem solid #b6272d;
          }
        }
      }
    }
  }
  .goos {
    width: 100%;
    display: flex;
    padding: 0.3rem;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
  }
}
</style>


